.shake:hover {
  transform: rotate(0deg) scale(1);
  animation: shake 300ms infinite;
}

.bob:hover {
  transform: rotate(0deg) scale(1);
  animation: bob 300ms infinite;
}

/*Contact*/
#contact-details p {
  text-shadow: rgba(255, 255, 255, 0.1647058824) 2px 2px 2px;
}

/*Tech Stack*/
.tech-stack {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: rgba(255, 255, 255, 0.05);
}

.tech {
  transform: rotate(0deg) scale(1);
  animation: shake 300ms ease-in-out;
}

.tech:hover {
  animation: shake 300ms infinite;
  cursor: pointer;
}

/*Back*/
/*Nav*/
#nav p, #back, #coming-soon {
  position: relative;
  color: inherit;
  text-decoration: none;
  line-height: 34px;
}
#nav p:before, #nav p:after, #back:before, #back:after, #coming-soon:before, #coming-soon:after {
  content: "";
  position: absolute;
  z-index: 50;
  transition: transform 0.5s ease;
}

.nav-effect, #back {
  padding-top: 10px;
  transition: all ease-in-out 300ms;
}
.nav-effect:before, #back:before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: scaleX(0);
}
.nav-effect:hover:before, #back:hover:before {
  transform: scaleX(1);
}

/*Background*/
.area {
  z-index: -1;
  width: 100%;
  height: 100%;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: radial-gradient(circle at center, #8f94fb, #4e54c8);
  animation: animate 25s linear infinite;
  backdrop-filter: blur(10px);
  background-size: 200%;
  bottom: -150px;
  z-index: 1;
}
.circles li::after {
  content: "";
  position: absolute;
  top: 32%;
  left: 22%;
  transform: translate(-50%, -50%);
  width: 18%;
  height: 15%;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 100%;
  overflow: hidden;
  z-index: 2;
}
.circles li::before {
  content: "";
  position: absolute;
  top: 22%;
  left: 30%;
  transform: translate(-50%, -50%);
  width: calc(10% - 2px);
  height: calc(10% - 2px);
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  z-index: 2;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(-1000px) rotate(0deg);
    opacity: 1;
    filter: blur(0);
    border-radius: 100%;
  }
  50% {
    transform: translateY(-500px) rotate(20deg);
    opacity: 0.6;
    filter: blur(0);
    border-radius: 100%;
  }
  75% {
    transform: translateY(-250px) rotate(0deg);
    opacity: 0;
    filter: blur(2px);
    border-radius: 60%;
  }
  100% {
    transform: translateY(0) rotate(0deg);
    opacity: 0;
    filter: blur(2px);
    border-radius: 60%;
  }
}
@keyframes bob {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes shake {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(15deg);
  }
  75% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0);
  }
}

